@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Show{
  width: 600px;
  margin: 0 auto;
  text-align: center;
  background-color: #fffcf9;
  border: 1px solid #c7d5db;
  margin-top: 10px;
  border-bottom: none;
  padding-bottom: 12px;
}
.Show-img{
  width: 100%;
  padding-bottom: 16px;
}
.row{
  margin: 0 auto;
  width: 600px;
}
.Show-Title{
  padding: 17px;
}
.Show-text{
  margin-bottom: 17px;
}
.Show-delete-button{
  margin: 4px;
  margin-left: 0;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  color: rgb(240, 56, 35);
}
.Show-delete-button:hover{
  margin: 4px;
  margin-left: 0;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  color: rgb(196, 25, 6);
}
.Show-buttons{
  width: 600px;
  margin: 0 auto;
  background-color: #fffcf9;
  border: 1px solid #c7d5db;
  margin-top: 0;
  margin-bottom: 25px;
  padding: 7px;
}


.Create-container{
  max-width:450px;
  width:100%;
  margin:0 auto;
  margin-top: 160px;
  position:relative;
  padding: 40px;
  background-color: #ffffff;
  color: #f04900;
  border: 2px solid #c7d5db;
}
fieldset input[class*="Create-input"] {
	border: 1px solid #c7d5db;
	margin: 0 0 10px;
	min-width: 100%;
	padding: 2px;
	width: 100%;
  font-weight: 100;
}
input[class*="Create-input"] {
  width: 100%;
  height: 35px;
}
.Create-button{
  background-color:rgb(13, 230, 21);
  border: none;
  color: White;
  text-transform: uppercase;
}
.Create-button:hover{
  background-color: rgba(13, 230, 21, 0.6);
  border: none;
  color: White;
  text-transform: uppercase;
}
textarea[class*="Create-input"]  {
  border: 1px solid #c7d5db;
  font-weight: 100;
}

::-webkit-input-placeholder{
  color: #c7d5db;
  text-transform: uppercase;
}

:-ms-input-placeholder{
  color: #c7d5db;
  text-transform: uppercase;
}

::-ms-input-placeholder{
  color: #c7d5db;
  text-transform: uppercase;
}

::placeholder{
  color: #c7d5db;
  text-transform: uppercase;
}
.Create-h3{
  color: rgb(13, 230, 21);
  font-weight: 500;
  border-bottom: 1px solid rgb(13, 230, 21);
  margin-bottom: 27px;
}

.Edit-p{
  margin-bottom: 0;
  margin-top: 6px;
  color: #f04900;
  font-weight: 500;
  text-transform: uppercase;
}
.Edit-container{
  max-width:450px;
  width:100%;
  margin:0 auto;
  margin-top: 160px;
  position:relative;
  padding: 40px;
  background-color: #ffffff;
  color: #f04900;
  border: 2px solid #c7d5db;
}
fieldset input[class*="Edit-input"] {
	border: 1px solid #c7d5db;
	margin: 0 0 10px;
	min-width: 100%;
	padding: 2px;
	width: 100%;
  font-weight: 100;
}
input[class*="Edit-input"] {
  width: 100%;
  height: 35px;
}
textarea[class*="Edit-input"]  {
  border: 1px solid #c7d5db;
    font-weight: 100;
}
.Edit-button{
  background-color:#f05d23;
  border: none;
  color: White;
  text-transform: uppercase;
}
.Edit-button:hover{
  background-color: rgba(240, 93, 35, 0.6);
  border: none;
  color: White;
  text-transform: uppercase;
}
.Edit-h3{
  border-bottom: 1px solid #f05d23;
}

.Navbar-Ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgba(58, 64, 90);
  border-bottom: 4px solid #f05d23;
  margin-bottom: 95px;
}
.Navbar-Li-logo {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  float:left;
}
.Navbar-Li{
    float: left;
}
.Navbar-Li-right{
  float:right;
}
.Navbar-Li a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-family: 'Oswald', sans-serif;
  font-weight: 500;

}
.Navbar-Li-right a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 7px;
  text-decoration: none;
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
}
.Navbar-Li a:not(.active){
  background-color: rgba(58, 64, 90);
}

.active{
  background-color:  #f05d23;
}
.Navbar-link {
  color: #61dafb;
}
.Navbar-Li-logo i{
 color: rgb(13, 230, 21);
 font-size: 20px;
}

.Error{
  text-align: center;
  margin-top: 220px;
}
.Error-icon{
  font-size: 200px;
  color:rgb(241, 116, 25);
  text-align: center;

}
.Error-text{
  font-size: 44px;
  color:rgb(241, 116, 25);
}

